import React, { Component } from "react";
class Delete extends Component {
  constructor(props) {
    super(props);
    this.state = {
      images: [],
    };
  }
  state = {};
  fetchImages = () => {
    fetch("https://server-for-mcgill-display.herokuapp.com/delete")
      .then((response) => {
        return response.json();
      })
      .then((body) => {
        let elementstring = body.message;
        this.setState({ images: elementstring.trim().split(/\s+/) });
        console.log(this.state.images)

      })
      .catch((error) => {
        console.log("Error");
      });
  }
  render() {
    return (
      <div>
        <h1>View and delete current posts</h1>
        <button onClick={this.fetchImages}>Will fetch images</button>
        <div className="card-container">
          <CardsForDisplay images = {this.state.images} />
          <CardsForDisplay images = {["ssdasdasdasdasdasdasdad"]} />
        </div>
        
      </div>
    );
  }
}
function CardsForDisplay(props){
  let cardList = []
  let images = props.images;
    images.forEach(element => {
    console.log(element)
    cardList.push(
      <div className="card">
        <ul>
        <li>Start date year: {element[0] + element[1] + element[2] + element[3]}</li>
        <li>Start date month: {element[5] + element[6]}</li>
        <li>End date year: {element[8] + element[9] + element[10] + element[11]}</li>
        </ul>
        <img src={element}></img>
      </div>
    )
  });
  return cardList;
}
export default Delete;
